/**
 * type of keys like 0 to 9 and .
 * @type {string}
 */
export const NUMBER = 'NUMBER';

/**
 * type of keys like AC, C, Off
 * @type {string}
 */
export const SYSTEM = 'SYSTEM';

/**
 * type of keys like +, -, , X, , +/-, %, =
 * @type {string}
 */
export const ARITHMETIC = 'ARITHMETIC';

/**
 * type of keys like MC, MR, M-, M+
 * @type {string}
 */
export const MEMORY = 'MEMORY';



